.form__save-button_theme_dark {
  background: #FFFFFF;
  color: #000000;
  transition: opacity .3s ease;
  margin-top: 216px;
  margin-bottom: 0;
}

.form__save-button_theme_dark:hover {
  opacity: 0.85;
}

.form__save-button_theme_dark:focus {
  opacity: 0.85;
}

@media screen and (max-width: 768px) {
  .form__save-button_theme_dark {
    margin-top: 173px;
  }
}
